.title {
    font-weight: 600;
    font-size: 88px;
    line-height: 80%;
    letter-spacing: -0.02em;
    text-align: center;
    background: radial-gradient(85.18% 257.73% at 48.35%  49.66%, rgb(255 255 255) 0%, rgb(255 255 255) 100%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
}

.subtitle {
    font-weight: 500;
    font-size: 24px;
    line-height: 140%;
    color: $mainc-w70;
}

@media (max-width: 1679px) {
    .container {
        max-width: 1320px;
    }

    .title {
        font-size: 84px;
    }
}

@media (max-width: 1440px) {
    .container {
        max-width: 1120px;
    }

    .title {
        font-size: 80px;
    }
}

@media (max-width: 1200px) {
    .container {
        max-width: 968px;
    }

    .title {
        font-size: 74px;
    }
}

@media (max-width: 992px) {
    .container {
        max-width: 648px;
    }

    .title {
        font-size: 70px;
        line-height: 110%;
    }

    .subtitle {
        font-size: 16px;
    }
}

@media (max-width: 767px) {
    .container {
        max-width: 100%;
        padding: 0 20px;
    }
}

@media (max-width: 576px) {
    .container {
        &_nopdg {
            padding: 0;
        }
    }

    .title {
        font-size: 40px;
    }

    .subtitle {
        max-width: 100%;
        padding: 0 20px;
        font-weight: 500;
        font-size: 14px;
        line-height: 130%;
        letter-spacing: -0.02em;
    }
}
